




































export default {
	data() {
		return {
			faceUrl: '../../static/icons/logo.png',
			userInfo: {},
		}
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: "Profile"
		});

		var userInfo = this.getGlobalUserInfo();
		if (userInfo == null) {
			return;
		} else {
			this.userInfo = userInfo;
		}
		// console.log(userInfo)
	},
	methods: {
		changeFaceImage: function() {
			var userInfo = this.userInfo;
			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					var tempFilePaths = res.tempFilePaths;
					console.log(tempFilePaths);
					uni.showLoading({
						title: 'Uploading...'
					})

					uni.uploadFile({
						url: that.$serverUrl + '/user/uploadFace?userId=' + userInfo.id,
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							var data = JSON.parse(res.data);
							console.log(data.data);
							uni.hideLoading();
							if (data.status == 200) {
								uni.showToast({
									title: 'Success!',
									icon: "success"
								})
								var imageUrl = data.data;
								that.faceUrl = that.$serverUrl + imageUrl;
							} else if (data.status == 500) {
								uni.showToast({
									title: data.msg
								})
							}
						}
					});
				}
			});
		},
		
		goToLogin: function(){
			uni.navigateTo({
				url: '../login/login',
			})
		},
		
		logout: function() {
			var that = this;
			uni.request({
				url: that.$serverUrl + '/logout?userId' + this.userInfo.id,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if (status == 200) {
						uni.showToast({
							icon: 'none',
							title: 'Logout'
						});
						that.removeGlobalUserInfo();
						uni.navigateTo({
							url: '../login/login',
						});
					} else if (status == 500) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
					}
				},
			});
		}
	}
}
